import * as t from '../actions/types'

type ItemsType = {
   [key: string]: {
      items: Array<t.AddedPizza>
      totalPrice: number
   }
}

type CartStateType = {
   items: ItemsType,
   totalPrice: number,
   totalCount: number,
}

const initialState: CartStateType = {
	items: {},
	totalPrice: 0,
	totalCount: 0
};

const getAllPizzas = (newItems: object) => {
	// –î–æ—Å—Ç–∞–µ–º –∏–∑ –æ–±—ä–µ–∫—Ç–∞ newItems –ø–æ –∫–ª—é—á—É items –º–∞—Å—Å–∏–≤—ã –ø–∏—Ü—Ü
	const itemsArr = Object.values(newItems).map((obj) => obj.items);
	// –æ–±—ä–µ–¥–∏–Ω—è–µ–º –≤—Å–µ –º–∞—Å—Å–∏–≤—ã –≤ –æ–¥–∏–Ω
	return [].concat.apply([], itemsArr);
};

const getTotalPrice = (arr: any) => arr.reduce((sum: number, obj: {price: number}) => obj.price + sum, 0);

const cartReducer = (state = initialState, action: t.CartActions): CartStateType => {
	switch (action.type) {
		case t.ADD_PIZZA_TO_CART:
			// –ø—Ä–æ–≤–µ—Ä—è–µ–º –µ—Å—Ç—å –ª–∏ –≤ items –ø–æ –∫–ª—é—á—É [id] –æ–±—ä–µ–∫—Ç—ã, –µ—Å–ª–∏ –Ω–µ—Ç—É —Ç–æ –≤–æ–∑–≤—Ä–∞—â–∞–µ–º –Ω–æ–≤—ã–π
			// –µ—Å–ª–∏ –µ—Å—Ç—å —Ç–æ –±–µ—Ä–µ–º –≤—Å–µ —Å—Ç–∞—Ä—ã–µ –æ–±—ä–µ–∫—Ç—ã –∏ –¥–æ–±–∞–≤–ª—è–µ–º –Ω–æ–≤—ã–π –æ–±—ä–µ–∫—Ç
			const currentPizzasItems = state.items[action.payload.id]
				? [...state.items[action.payload.id].items, action.payload]
				: [action.payload];

			const newItems = {
				...state.items,
				[action.payload.id]: {
					items: currentPizzasItems,
					totalPrice: getTotalPrice(currentPizzasItems)
				}
			};
			const allPizzas = getAllPizzas(newItems);
			const totalPrice = getTotalPrice(allPizzas);

			return {
				...state,
				items: newItems,
				totalCount: allPizzas.length,
				totalPrice
			};

		case t.REMOVE_CART_ITEM: {
			const newItems = {
				...state.items
			};
			const currentTotalPrice = newItems[action.payload].totalPrice;
			const currentTotalCount = newItems[action.payload].items.length;
			delete newItems[action.payload];

			return {
				...state,
				items: newItems,
				totalPrice: state.totalPrice - currentTotalPrice,
				totalCount: state.totalCount - currentTotalCount
			};
		}

		case t.CLEAR_CART:
			return initialState;

		case t.PLUS_CART_ITEM: {
			const newObjItems = [
				...state.items[action.payload].items,
				state.items[action.payload].items[0]
         ];
         
         const newItems = {
				...state.items,
				[action.payload]: {
					items: newObjItems,
					totalPrice: getTotalPrice(newObjItems)
				}
			};
			const allPizzas = getAllPizzas(newItems);
			const totalPrice = getTotalPrice(allPizzas);
			return {
				...state,
				items: newItems,
				totalCount: allPizzas.length,
				totalPrice
			};
		}

		case t.MINUS_CART_ITEM: {
			const oldObjItems = state.items[action.payload].items;
         const newObjItems = oldObjItems.length > 1 ? oldObjItems.slice(1) : oldObjItems;
         const newItems = {
				...state.items,
				[action.payload]: {
					items: newObjItems,
					totalPrice: getTotalPrice(newObjItems)
				}
			};
			const allPizzas = getAllPizzas(newItems);
			const totalPrice = getTotalPrice(allPizzas);
			return {
				...state,
				items: newItems,
				totalCount: allPizzas.length,
				totalPrice
			};
		}

		default:
			return state;
	}
};

export default cartReducer;
